"use client";

import { useForm } from "react-hook-form";
import { useRouter } from "next/navigation";
import { useState } from "react";
import { amenitiesList } from "@/lib/roomsData";

type SearchFormData = {
  date: string;
  startTime: string;
  endTime: string;
  capacity: number;
  amenities: string[];
};

export default function HomePage() {
  const { register, handleSubmit, formState: { errors }, watch } = useForm<SearchFormData>({
    defaultValues: {
      amenities: []
    }
  });
  const router = useRouter();
  const [selectedAmenities, setSelectedAmenities] = useState<string[]>([]);

  const onSubmit = (data: SearchFormData) => {
    const query = new URLSearchParams({
      date: data.date,
      startTime: data.startTime,
      endTime: data.endTime,
      capacity: data.capacity.toString(),
      amenities: selectedAmenities.join(',')
    }).toString();
    router.push(`/search?${query}`);
  };

  const handleAmenityChange = (amenity: string, checked: boolean) => {
    if (checked) {
      setSelectedAmenities(prev => [...prev, amenity]);
    } else {
      setSelectedAmenities(prev => prev.filter(a => a !== amenity));
    }
  };

  return (
    <div className="mt-8">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold mb-4">Find Your Perfect Meeting Room</h1>
        <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
          Book meeting rooms effortlessly with our corporate booking system. 
          Search by date, time, capacity, and amenities to find the ideal space for your team.
        </p>
      </div>

      <div className="max-w-2xl mx-auto bg-card p-8 rounded-lg border shadow-sm">
        <h2 className="text-2xl font-semibold mb-6">Search Meeting Rooms</h2>
        
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block font-medium mb-2">Date</label>
              <input 
                type="date"
                {...register("date", { required: "Date is required" })}
                className="w-full px-4 py-3 border border-input rounded-md focus:outline-none focus:ring-2 focus:ring-ring"
                min={new Date().toISOString().split('T')[0]}
              />
              {errors.date && <span className="text-destructive text-sm mt-1 block">{errors.date.message}</span>}
            </div>

            <div>
              <label className="block font-medium mb-2">Capacity</label>
              <input 
                type="number"
                min="1"
                max="50"
                {...register("capacity", { 
                  required: "Capacity is required",
                  min: { value: 1, message: "Capacity must be at least 1" },
                  valueAsNumber: true 
                })}
                className="w-full px-4 py-3 border border-input rounded-md focus:outline-none focus:ring-2 focus:ring-ring"
                placeholder="Number of people"
              />
              {errors.capacity && <span className="text-destructive text-sm mt-1 block">{errors.capacity.message}</span>}
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block font-medium mb-2">Start Time</label>
              <input 
                type="time"
                {...register("startTime", { required: "Start time is required" })}
                className="w-full px-4 py-3 border border-input rounded-md focus:outline-none focus:ring-2 focus:ring-ring"
              />
              {errors.startTime && <span className="text-destructive text-sm mt-1 block">{errors.startTime.message}</span>}
            </div>

            <div>
              <label className="block font-medium mb-2">End Time</label>
              <input 
                type="time"
                {...register("endTime", { required: "End time is required" })}
                className="w-full px-4 py-3 border border-input rounded-md focus:outline-none focus:ring-2 focus:ring-ring"
              />
              {errors.endTime && <span className="text-destructive text-sm mt-1 block">{errors.endTime.message}</span>}
            </div>
          </div>

          <div>
            <label className="block font-medium mb-3">Amenities (Optional)</label>
            <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
              {amenitiesList.map((amenity) => (
                <label key={amenity} className="flex items-center space-x-2 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={selectedAmenities.includes(amenity)}
                    onChange={(e) => handleAmenityChange(amenity, e.target.checked)}
                    className="rounded border-input focus:ring-2 focus:ring-ring"
                  />
                  <span className="text-sm">{amenity}</span>
                </label>
              ))}
            </div>
          </div>

          <button 
            type="submit" 
            className="w-full py-4 bg-primary text-primary-foreground rounded-md font-semibold hover:bg-primary/90 transition-colors focus:outline-none focus:ring-2 focus:ring-ring"
          >
            Search Available Rooms
          </button>
        </form>
      </div>

      <div className="mt-16 grid grid-cols-1 md:grid-cols-3 gap-8 text-center">
        <div className="p-6">
          <div className="text-3xl font-bold text-primary mb-2">50+</div>
          <h3 className="font-semibold mb-2">Meeting Rooms</h3>
          <p className="text-muted-foreground">Various sizes and configurations available</p>
        </div>
        <div className="p-6">
          <div className="text-3xl font-bold text-primary mb-2">24/7</div>
          <h3 className="font-semibold mb-2">Availability</h3>
          <p className="text-muted-foreground">Book rooms anytime, anywhere</p>
        </div>
        <div className="p-6">
          <div className="text-3xl font-bold text-primary mb-2">100%</div>
          <h3 className="font-semibold mb-2">Digital</h3>
          <p className="text-muted-foreground">Paperless booking and management</p>
        </div>
      </div>
    </div>
  );
}
